@import url(https://fonts.googleapis.com/css2?family=Fruktur&family=Roboto:wght@400;700&display=swap);
#main-footer {
  background-color: #222;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 0;
  grid-gap: 10px;
  gap: 10px;
}

#main-footer p {
  color: #fff;
  font-size: 18px;
}

#main-footer a {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .1s;
}

#main-footer a:hover {
  -webkit-filter: brightness(.8);
          filter: brightness(.8);
}
#main-header {
  background-color: #222;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 100px;
}

#title {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

#title img:hover {
  animation: rotate .5s infinite alternate;
}

@keyframes rotate {
  0% {
      transform: rotate(20deg);
  }
  100% {
      transform: rotate(-20deg);
  }
}

#main-header h1 {
  color: #fff;
}

#main-header h1 span {
  color: #ff3d00;
}

#scoreboard {
  font-size: 18px;
}

#scoreboard #current {
  color: #ff3d00;
}

#scoreboard #best {
  color: #fff;
}

@media(max-width: 550px) {
  #main-header {
    flex-direction: column;
  }
}
.card {
  border: 2px solid #222;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 120px;
  box-shadow: 5px 5px 5px #222;
  transition: all .2s;
}

.card:hover {
  transform: scale(1.1);
}

.card .sprite {
  border-bottom: 2px solid #222;
}

.card .pokemon-name {
  padding: 5px 0;
}
#cardboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px auto 31px;
}

#cardboard #cards {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
  width: 80vw;
}

#loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  font-size: 18px;
}

#loading img {
  animation: rotate .5s infinite alternate;
}

@keyframes rotate {
  0% {
      transform: rotate(20deg);
  }
  100% {
      transform: rotate(-20deg);
  }
}

@media(min-width: 850px) {
  #cardboard #cards {
    width: 70vw;
  }
}

@media(min-width: 972px) {
  #cardboard #cards {
    width: 60vw;
  }
}

@media(min-width: 1134px) {
  #cardboard #cards {
    width: 50vw;
  }
}

@media(min-width: 1360px) {
  #cardboard #cards {
    width: 40vw;
  }
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

#root {
  font-family: 'Roboto', sans-serif;
}

/* custom scroll */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #c7c7c7; 
  background: #e7e6e6;
}
 
::-webkit-scrollbar-thumb {
  background: #222;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #353545; 
}
